@import './variable.scss';

.dashboard-market-info {
  display: none;
  margin-bottom: 5px;
  @media (max-width: 984px) {
    display: inline-block;
    width: 100%;
  }
  .info-card-group {
    display: none;
    flex-direction: row;
    justify-content: space-between;
    .info-card {
      background-color: white;
      padding: 20px;
      margin: 5px;
      border-radius: 10px;
      width:100%;
      box-shadow: 0px 1px 50px -10px rgba(0, 0, 0, 0.125);
    }
    @media (max-width: 984px) {
      display: flex;
    }
  }
}
.wallet-container, .explorer-component {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  .container{
    padding: 20px 0px;
    @media (max-width: 767px) {
      padding: 20px 5px;
    }
  }
  .dashboard-session {
    padding: 10px 0px;
    .header.session-header {
      margin-left: 10px;
      text-align: left;
      font-family: "Open Sans", "Roboto", sans-serif;
      text-transform: uppercase;
      font-weight: 800;
      letter-spacing: 1.3px;
      color: rgba(0, 0, 0, 0.3);
      font-size: 1.1rem;
    }
  }
  .balance-cards {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .balance-card-container {
      display: flex;
      flex-direction: column;
      padding: 3px;
      margin: 5px;
      background-color: white;
      border-radius: 10px;
      box-shadow: 0px 1px 50px -10px rgba(0, 0, 0, 0.125);
      @media screen and (min-width: 1200px) {
        width: 100%;
      }
      &:hover {
        box-shadow: 0px 1px 50px -10px rgba(0, 0, 0, 0.125);
        .balance-card {
          transition: 0.2s all ease;
        }
      }
      .fluid.button.wallet-action-button {
        background: rgba(0, 0, 0, 0.08);
        &:hover{
          color: $main-color;
          background: rgba(0, 0, 0, 0.1);
        }
        &:active{
          background: rgba(0, 0, 0, 0.15);
        }
      }
      .balance-card {
        overflow: hidden;
        position: relative;
        height: 160px;
        width: 295px;
        padding: 20px;
        margin-bottom: 3px;
        border-radius: 8px;
        background-color: white;
        // @media screen and (min-width: 1500px) {
        //   width: 330px;
        // }
        img {
          height: 50px;
        }
        &.eth-card {          
          color: rgb(40, 40, 40);
        }
        &.hodlc-card {
        
        }
        &.hodlt-card {
        
        }
        .top {
          display: flex;
          flex-direction: row;
          align-items: center;
         
          .title {
            // color: white;
            color: rgb(40, 40, 40);
            font-weight: 500;
            padding: 0 10px;
            font-size: 1.5rem;
            font-weight: 700;
          }
        }
        .bottom {
          margin-top: 18px;

          .number {
            font-size: 1.3rem;
            color: black;
            font-weight: 500;
            padding-bottom: 5px;
            .fiat {
              margin-top: 5px;
              display: inline-block;
              color: rgba(0, 0, 0, 0.5);
              font-size: 1.1rem;
            }
            @media screen and (max-width: map-get($breakpoints, large)) {
              font-size: 1.5rem;
              padding: 0.5rem 0;
            }
            @media screen and (max-width: map-get($breakpoints, medium)) {
              font-size: 1.3rem;
              padding: 0.2rem 0;
            }
            @media screen and (max-width: map-get($breakpoints, small)) {
              font-size: 1rem;
            }
          }
          .t-group {
            display:flex;
            flex-direction:row;
            align-items: center;
            padding-top: 5px;
            .number{
              font-size: 1.1rem;
            }
            .ui.button {
              margin: 0px;
              opacity: 0.7;
              color: black;
              border-radius: 8px;
              &:hover {
                opacity: 0.8;
              }
              &:active {
                transform: scale(0.97);
              }
            }
          }
          @media screen and (max-width: map-get($breakpoints, large)) {
            margin-top: 30px;
          }
          @media screen and (max-width: map-get($breakpoints, medium)) {
            margin-top: 20px;
          }
        }
        .logo-mark {
          position: absolute;
          opacity: 0.3;
          top: 45px;
          right: 15px;
          height: 150px;
          @media screen and (max-width: map-get($breakpoints, large)) {
            width: 200px;
            right: 14%;
          }
          @media screen and (max-width: map-get($breakpoints, medium)) {
            width: 150px;
            right: 20px;
          }
        }
        @media screen and (max-width: map-get($breakpoints, large)) {
          width: 100%;
          height: 180px;
        }
        @media screen and (max-width: map-get($breakpoints, medium)) {
          height: 160px;
          width: 100%;
        }
        @media screen and (max-width: map-get($breakpoints, small)) {
          height: 180px;
          width: 100%;
        }
      }
      .btn-group {
        padding: 0px;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        .ui.button {
          width: 100%;
          margin: 0px;
          opacity: 0.7;
          color: black;
          border-radius: 8px;
          &:first-child {
            margin-right: 5px;
          }
          &:hover {
            opacity: 0.8;
          }
          &:active {
            transform: scale(0.97);
          }
        }
        @media screen and (max-width: map-get($breakpoints, medium)) {
          flex-direction: column;
          justify-content: center;
          .ui.button:first-child {
            margin-bottom: 8px;
          }
        }
      }
      @media screen and (max-width: map-get($breakpoints, large)) {
        flex-direction: column;
        justify-content: center;
      }
    }

    @media screen and (max-width: map-get($breakpoints, large)) {
      flex-direction: column;
      justify-content: center;
    }
    @media screen and (max-width: map-get($breakpoints, medium)) {
      flex-direction: column;
      justify-content: center;
    }
    @media screen and (max-width: map-get($breakpoints, small)) {
      padding: 5px;
    }
  }
  .event-list {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 1px 50px -10px rgba(0, 0, 0, 0.125);
    padding: 10px;
    margin: auto 3px;
    .event-container {
      padding: 10px;
      margin-bottom: 5px;
      background-color: rgba(0, 0, 0, 0.02);
      &.event-item:last-child {
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }
      .hash {
        width: 100px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .action-label {
        display: block;
        color: white;
        font-weight: 700;
        letter-spacing: 0.3px;
        color: white;
        text-align: left;
        background: rgba(0, 0, 0, 0.05);
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        &.deposit {
          color: rgb(219, 88, 255);
        }
        &.withdraw {
          color: rgb(228, 100, 179);
        }
        // issue
        &.To.HODLC {
          color: rgb(15, 199, 220);
        }
        // redeem
        &.To.HODLT {
          color: rgb(244, 90, 107);
        }
        &.sell.HODLC {
          color: white;
          background-color: $red-color;
          opacity: 0.6;
        }
        &.buy.HODLC {
          color: white;
          background-color: $green-color;
          opacity: 0.7;
        }
      }
      .status {
        display: flex;
        flex-direction: row;
        .dot {
          display: inline;
          margin-top: -0.4px;
          margin-right: 5px;
          font-size: 1rem;
          color: #00d087;
        }
      }
      .sm-label {
        display: none;
        margin-bottom: 2px;
        font-weight: 700;
        @media screen and (max-width: 768px) {
          display: block;
        }
      }
      .amount {
        text-align: right;
        line-height: 20px;
        .light {
          opacity: 0.2;
        }
        .unit-span {
          display: inline-block;
          width: 70px;
          padding-left: 20px;
          text-align: left
        }
        span {
          font-weight: 700;
        }
        @media screen and (max-width: 768px) {
          text-align: left;
        }
      }
      .ui.stackable.grid > .wide.column {
        @media screen and (max-width: 768px) {
          padding: 5px !important;
        }
      }
      .ui.stackable.grid {
        @media screen and (max-width: 768px) {
          padding: 10px;
        }
      }
      @media screen and (max-width: 768px) {
        margin-top: 10px;
      }
    }
    .history-header {
      background: white;
      font-weight: 700;
      text-transform: capitalize;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      .hash {
        width: 100px;
      }
      .amount{
        text-align: right;
      }
      @media screen and (max-width: 768px) {
        display: none;
      }
    }
  }
}
