.dashboard-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  width: 100%;
  padding: 55px 0 0 60px;
  overflow:scroll;
  background-color:$background-color;
  &::-webkit-scrollbar-track {
    //full
    background-color: transparent;
    border-radius: 20px;
  }

  &::-webkit-scrollbar-thumb {
    //bar
    opacity: 1;
    background-color: $main-color;
    cursor: pointer;
  }

  &::-webkit-scrollbar {
    width: 0px;
  }
}